import React from "react";


function TableRows(props) {
    return (

        <tr>
            <td>
                <img alt="employee" src={props.image} />
            </td>
            <td>{props.name}</td>
            <td>{props.phone}</td>
            <td>{props.email}</td>
            <td>{props.dob.date}</td>
        </tr>

    )
}

export default TableRows;